<template>
  <mt-popup
    class="pup"
    v-if="init.key"
    v-model="init.key"
    :modal="false"
    position="bottom"
    @click.native="close">
        <slot name="timebox"></slot>
    </mt-popup>
</template>
<script>
    export default{
        props:['init'],
        methods:{
            close(){
               // this.init.key=false;
            }
        }
    }
</script>
<style scoped lang="scss">
.pup{
    width:100%;
    background-color:rgba(0,0,0,.5);
    height:100%;
}
</style>




